.log-in-to {
  position: absolute;
  top: 0;
  left: 0;
  font-weight: 900;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 290px;
  height: 42px;
}
.title {
  align-self: stretch;
  position: relative;
  height: 42px;
  text-align: center;
  font-size: 24px;
}
.name {
  position: absolute;
  top: 15px;
  left: 12px;
  font-weight: 600;
}
.email,
.txtname {
  position: relative;
}
.txtname {
  align-self: stretch;
  border-radius: 8px;
  background-color: #2b2b2b;
  height: 45px;
  color: #585858;
}
.email {
  font-weight: 600;
}
.txtemail {
  align-self: stretch;
  border-radius: 8px;
  background-color: #2b2b2b;
  height: 45px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding: 15px 12px;
  box-sizing: border-box;
  color: #585858;
}
.password1 {
  margin: 0;
}
.password {
  position: relative;
  font-weight: 600;
  display: inline-block;
  width: 65px;
  height: 12px;
  flex-shrink: 0;
}
.btnlogin,
.txtpassword {
  align-self: stretch;
  border-radius: 8px;
  height: 45px;
  display: flex;
  flex-direction: row;
}
.txtpassword {
  background-color: #2b2b2b;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 15px 12px;
  box-sizing: border-box;
  color: #585858;
}
.btnlogin {
  background-color: #fff;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: #000;
}
.or-child {
  position: relative;
  border-top: 1px solid #5f5f5f;
  box-sizing: border-box;
  width: 86px;
  height: 1px;
}
.or,
.or-continue-with {
  display: flex;
  align-items: center;
}
.or-continue-with {
  position: relative;
  justify-content: center;
  width: 108px;
  height: 20px;
  flex-shrink: 0;
}
.or {
  align-self: stretch;
  height: 32px;
  flex-direction: row;
  justify-content: space-between;
  padding: 10px 0;
  box-sizing: border-box;
  text-align: center;
  color: #5f5f5f;
}
.frame-icon {
  width: 20px;
  height: 20px;
  overflow: hidden;
  flex-shrink: 0;
}
.frame-icon,
.log-in1 {
  position: relative;
}
.btngoogle {
  align-self: stretch;
  border-radius: 8px;
  background-color: #222;
  border: 1px solid #343434;
  box-sizing: border-box;
  height: 45px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 8px;
}
.terms {
  text-decoration: underline;
}
.by-signing-up-container {
  position: absolute;
  top: 0;
  left: 0;
  font-weight: 600;
}
.signupagreement {
  position: relative;
  width: 317px;
  height: 15px;
  text-align: center;
  color: #5f5f5f;
}
.form {
  text-decoration: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: 20px;
  color: inherit;
}
.already-have-an {
  font-weight: 600;
}
.log-in3 {
  color: #fff;
}
.already-have-an-container {
  position: absolute;
  top: 0;
  left: 64px;
}
.frm-sign-up {
  position: relative;
  background-color: #1d1d1d;
  width: 100%;
  height: 982px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 100px 543px;
  box-sizing: border-box;
  text-align: left;
  font-size: 12px;
  color: #fff;
  font-family: Inter;
}
